html, body{
  max-width: 98%;
  height: 100%;
  overflow-x: hidden;
}
body{
  background-color: #fff;
  margin: 0;
}
p{
  font-family: 'Rubik-Light';
}
a{
  text-decoration: none;
  color: #333;
}
a:hover{
  color: #f33;
}
a:visited{
  color: #333;
}

.App{
  margin-left: 60px;
  margin-right: 60px;
  margin-top: 20px;

}

@font-face {
    font-family: Bromello;
    src: url('./fonts/bromello.ttf');
}
@font-face {
    font-family: Rubik-Light;
    src: url('./fonts/Rubik-Light.ttf');
}
@font-face {
    font-family: Rubik-Medium;
    src: url('./fonts/Rubik-Medium.ttf');
}
@font-face {
    font-family: Arial-Rounded;
    src: url('./fonts/Arial-Rounded.ttf');
}

.name{
  font-family: Bromello;
  font-size: 60px;
  padding: 0;
  margin: 0;
  margin-top: 30px;
  border-bottom: 3px solid;
  max-width: 250px;
}

.mainBody{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: auto;
  margin: 0;
  padding: 0;
  height: 100%
}

.first{
  grid-column: 1/3;
  grid-row: 1/2;
}
.menu{
  
}
.canvas-wrap {
  position:relative;
  } /* Make this a positioned parent */
.canvas-overlay {
  position:absolute;
  top:20px;
  left:30px;
}
.second{
  grid-column: 3/6;
  grid-row: 1/2;
  margin-left: -20px;
}
.homeComp{
  margin-top: 70px;
  width: 770px;
  height: 90%;
}
.frame{
  width: 99%;
  height: 86%;
  border: 0;
  border-radius: 5px;
}
.videoGalleryHead{
  margin-top: -2px;
}
.blog-title{
  font-size: 2.1em;
  padding-top: 20px;
  padding-left: 20px;
  margin: 0;
  font-family: 'Rubik-Light';
  max-width: 90%;
  color: #333;
  line-height: 40px;
}
.date{
  font-family: 'Rubik-Medium';
  text-transform: uppercase;
  padding-top: 30px;
  padding-left: 20px;
  margin: 0;
  color: #939393;
  font-weight: bold;
}
.blog-body{
  font-family: 'Rubik-Light';
  font-size: 17px;
  padding-top: 20px;
  padding-left: 20px;
  margin: 0;
  max-width: 93%;
  height: 380px;
  overflow-y: scroll;
  scroll-behavior: smooth;
}
.sample-blog-img{
  image-resolution: from-image;
  padding-left: 20px;
}
.blog-body::-webkit-scrollbar {
    display: none;
}
.top-post-title{
  padding-top: 0px;
  font-family: 'Rubik-Medium';
  text-transform: uppercase;
  font-weight: bold;
}
.blogImage{
  background-image: url("./blog.jpg");
  background-size: cover;
  width: 80%;
  height: 100px;
  border-radius: 5px;
  margin-bottom: 8px;
  -webkit-box-shadow: 0px 0px 15px 2px #ddd;
          box-shadow: 0px 0px 15px 2px #ddd;
}
.blogImage1{
  background-image: url("./1.jpg");
  background-size: cover;
  width: 80%;
  height: 100px;
  border-radius: 5px;
  margin-bottom: 8px;
  -webkit-box-shadow: 0px 0px 15px 2px #ddd;
          box-shadow: 0px 0px 15px 2px #ddd;
}
.topBlogTitle, .topBlogTitle1{
  font-family: 'Rubik-Light';
  font-size: 20px;
  font-weight: bold;
  color: #333;
  z-index: 2;
  position: absolute;
  bottom: -5px;
  background-color: #eee;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 0 5px 5px 0px;
  max-width: 60%;
}
.blog-mini-screen {
  position: relative;
  text-decoration: line-through;
}
.dots{
  margin-top: -60px;
  margin-left: -3px;
  font-size: 50px;
  font-family: 'Arial-Rounded';
  display: none;
}

.image{
  background-image: url("./components/1.jpg");
  background-size: cover;
  height: 500px;
  margin-top: 30px;
  padding: 20px;
  -webkit-box-shadow:0 0 10px #aaa;
          box-shadow:0 0 10px #aaa;
}

.description{
  font-family: 'Rubik';
  padding-right: 10px;
  max-width: 400px;
}

.work{
  background-color: transparent;
  border-radius: 20px;
  border: 0px;
  color: black;
  font-size: 20px;
  padding: 0;
  font-family: 'Bromello';
}
.copyright{
  font-family: 'Rubik-Medium';
  margin-top: 80px;
  font-size: 14px;
  text-transform: uppercase;
  margin-left: 0px;
  color: #777;
  text-align: center;
  display: none;
}
.imp{
  color: red;
}

/*Responsive elements*/
@media only screen and (max-width:768px) {
  .App{
    margin:auto;
    padding: 10px;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 5px;
  }
  .mainBody{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
    grid-gap: 10px;
    margin: 0;
    padding: 0;
    max-width: 98%;
  }
  .name, .description {
    max-width: auto;
  }
  .first{
    grid-column: auto;
    grid-row: 1/2;
  }
  .second{
    grid-column: auto;
    grid-row: 2/3;
  }
  .name{
    border-bottom: 0;
  }

  .blog-title{
    font-size: 1.5em;
    line-height: normal;
    padding-left: 5px;
  }
  .date{
    padding-left: 5px;
  }
  .blog-body{
    padding-left: 5px;
  }
  .top-post-title{
    padding-top: 10px;
  }
  .blogImage, .blogImage1{
    width: 100%;
    margin: auto;
    margin-bottom: 10px;
  }
  .topBlogTitle{
  }
  .dots{
    text-align: center;
    margin-top: -30px;
  }
  .copyright{
    text-align: center;
  }
  .homeComp{
    width: auto;
    padding-left: 20px;
  }
  .frame{
    width: 100%;
  }

}



